@border: #cdcdcd;
@background: #fcfcfc;
body{
  background-color: @background;
  .title{
    width: 100%;
    border-bottom: 1px solid @border;
  }
  label{
    text-align: right;
    font-weight: normal;
    line-height: 32px;
    &::after{
      content: "*";
      color: red;
    }
  }
  .ps{
    color: @border;
    line-height: 36px;
  }
}